function email(dict){
  $.ajax({
    type: "POST",
    url: "http://localhost:8080/send_email",
    data: { data: JSON.stringify(dict) },
    dataType: "JSON",
    contentType: "application/x-www-form-urlencoded",
    success: function(result){
        // var dat = decodeUnicode(result)
        
        console.log(result['code'])
        if(result['code']===200){
            alert('提交成功！系统已将问卷结果提交至数据中心，感谢您的支持！')
        }
        else{
            email(dict)
        }
    },
});
}
export default email